import { CatFlapDevice, Curfew } from "../types";

const constructDateWithTime = (from: Date, time: string) => {
  const newDateWithTime = new Date(from.getTime());
  newDateWithTime.setHours(
    parseInt(time.split(":")[0]), // hours
    parseInt(time.split(":")[1]), // minutes
    0, // seconds
    0 // miliseconds
  );
  return newDateWithTime;
};

const isInCurfew = (curfew: Curfew, current: Date) => {
  if (!curfew.enabled) {
    return false;
  }

  const lockTime = constructDateWithTime(current, curfew.lock_time);
  const unlockTime = constructDateWithTime(current, curfew.unlock_time);

  if (lockTime > current && lockTime > unlockTime && unlockTime > current) {
    return true;
  }

  if (lockTime > current && lockTime > unlockTime && unlockTime < current) {
    return false;
  }

  if (lockTime > current && lockTime < unlockTime && unlockTime > current) {
    return false;
  }

  if (lockTime < current && lockTime > unlockTime && unlockTime < current) {
    return true;
  }

  if (lockTime < current && lockTime < unlockTime && unlockTime > current) {
    return true;
  }

  if (lockTime < current && lockTime < unlockTime && unlockTime < current) {
    return false;
  }
};

const getEnabledCurfews = (catFlap: CatFlapDevice) =>
  catFlap.control.curfew.filter((f) => f.enabled);

export const isLocked = (catFlap: CatFlapDevice, atTime: Date = new Date()) => {
  const enabledCurfews = getEnabledCurfews(catFlap);
  if (enabledCurfews.length === 0) {
    return false;
  }

  return enabledCurfews.some((c) => isInCurfew(c, atTime));
};

export const getNextTime = (times: Date[], atTime: Date) => {
  const timesAfterAtTime = times.filter((t) => t > atTime);

  if (timesAfterAtTime.length > 0) {
    return new Date(Math.min(...timesAfterAtTime.map((t) => t.getTime())));
  } else {
    return new Date(Math.min(...times.map((t) => t.getTime())));
  }
};

export const getNextLockOrUnlockTime = (
  catFlap: CatFlapDevice,
  atTime: Date = new Date()
): string | false => {
  const enabledCurfews = getEnabledCurfews(catFlap);
  if (enabledCurfews.length === 0) {
    return false;
  }

  if (isLocked(catFlap, atTime)) {
    const unlockTimes = enabledCurfews.map((c) =>
      constructDateWithTime(atTime, c.unlock_time)
    );

    return getNextTime(unlockTimes, atTime).toLocaleTimeString(undefined, {
      timeStyle: "short",
    });
  } else {
    const lockTimes = enabledCurfews.map((c) =>
      constructDateWithTime(atTime, c.lock_time)
    );

    return getNextTime(lockTimes, atTime).toLocaleTimeString(undefined, {
      timeStyle: "short",
    });
  }
};
