import {
  getNextLockOrUnlockTime,
  isLocked,
} from "./client/helpers/curfewHelpers";
import { useAppDispatch, useAppSelector } from "./redux/store";
import { clearAuth, selectIsLoggedIn } from "./redux/authSlice";
import { useInfoQuery, useTimelineQuery } from "./client/api";
import { isDevelopment } from "./isDevelopment";
import {
  Avatar as AvatarBase,
  Divider as DividerBase,
  Button,
  Typography,
  Badge,
} from "@mui/material";
import { Login } from "./components/login/Login";
import React from "react";
import { CatFlapDevice, Direction, Photo } from "./client/types";
import HomeOutlined from "@mui/icons-material/HomeOutlined";
import ParkOutlined from "@mui/icons-material/ParkOutlined";
import {
  getDirectionDisplay,
  getLocationDisplay,
  isInside,
} from "./client/helpers/petHelpers";
import ArrowBack from "@mui/icons-material/ArrowBack";
import ArrowForward from "@mui/icons-material/ArrowForward";
import Visibility from "@mui/icons-material/Visibility";
import SensorDoorOutlined from "@mui/icons-material/SensorDoorOutlined";
import { Box } from "@mui/system";

const Avatar = (props: { src: string; alt: string }) => (
  <AvatarBase
    {...props}
    sx={{ width: 100, height: 100, alignSelf: "center" }}
    className="avatar"
  />
);

const Divider = () => <DividerBase sx={{ width: "80%", margin: 2 }} />;

const InIcon = () => (
  <HomeOutlined
    sx={{
      width: 40,
      height: 40,
      color: "white",
      background: "green",
      borderRadius: "50%",
    }}
  />
);

const OutIcon = () => (
  <ParkOutlined
    sx={{
      width: 40,
      height: 40,
      color: "white",
      background: "orange",
      borderRadius: "50%",
    }}
  />
);

const getPhoto = (photos: Photo[], id: number) =>
  photos.find((p) => p.id === id)?.location;

const App = () => {
  const dispatch = useAppDispatch();

  const isLoggedIn = useAppSelector(selectIsLoggedIn);

  const { data: infoData, isLoading: isInfoLoading } = useInfoQuery(undefined, {
    skip: !isLoggedIn,
  });
  const { data: timelineData, isLoading: isTimelineLoading } = useTimelineQuery(
    undefined,
    {
      skip: !isLoggedIn,
    }
  );
  const loading = isInfoLoading || isTimelineLoading;

  const handleLogout = () => {
    dispatch(clearAuth());
  };

  const catFlap = infoData?.data?.devices.find(
    (d) => d.product_id === 6
  ) as CatFlapDevice;
  const pets = infoData?.data?.pets;
  const timeline = timelineData?.data;
  const photos = infoData?.data?.photos;

  return (
    <>
      {!isLoggedIn && <Login />}
      {isLoggedIn && loading && <Typography>Loading data...</Typography>}
      {isLoggedIn && !loading && catFlap && (
        <>
          <Typography>
            The cat flap is {isLocked(catFlap) ? "locked" : "unlocked"}{" "}
            {getNextLockOrUnlockTime(catFlap) ? "until" : ""}{" "}
            {getNextLockOrUnlockTime(catFlap) ?? ""}
          </Typography>
          <Divider />
        </>
      )}
      {isLoggedIn &&
        !loading &&
        pets &&
        pets.map((pet) => (
          <React.Fragment key={pet.id}>
            <Badge
              overlap="circular"
              badgeContent={isInside(pet) ? <InIcon /> : <OutIcon />}
            >
              <Avatar src={pet.photo.location} alt={`${pet.name}`} />
            </Badge>
            <Typography variant="subtitle1">
              {pet.name} is {getLocationDisplay(pet)} (since{" "}
              {new Date(pet.position.since).toLocaleTimeString(undefined, {
                timeStyle: "short",
              })}
              )
            </Typography>
          </React.Fragment>
        ))}
      {isLoggedIn && !loading && timeline && photos && (
        <>
          <Divider />
          {timeline.map((t) => {
            const pet = t.pets[0];
            const photo = getPhoto(photos, pet.photo_id);
            const direction = t.movements[0].direction;
            const DirectionIcon =
              direction === Direction.In
                ? ArrowBack
                : direction === Direction.Out
                ? ArrowForward
                : Visibility;
            const locationDisplay =
              direction === Direction.In ? (
                <InIcon />
              ) : direction === Direction.Out ? (
                <OutIcon />
              ) : (
                <SensorDoorOutlined sx={{ width: 40, height: 40 }} />
              );
            return (
              <Box
                key={t.id}
                sx={{
                  display: "flex",
                  alignItems: "center",
                  paddingBottom: 1,
                }}
              >
                <Typography sx={{ paddingRight: 1 }} variant="caption">
                  {new Date(t.created_at).toLocaleTimeString(undefined, {
                    timeStyle: "short",
                  })}
                </Typography>
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    flexDirection:
                      direction === Direction.In ? "row-reverse" : "row",
                  }}
                >
                  {photo && <AvatarBase src={photo} alt={pet.name} />}
                  <DirectionIcon sx={{ width: 40, height: 40 }} />
                  {locationDisplay}
                </Box>
              </Box>
            );
          })}
        </>
      )}
      {isLoggedIn && !loading && (
        <>
          <Divider />
          <Button variant="contained" onClick={handleLogout}>
            Log out
          </Button>
        </>
      )}
      <Divider />
      <Typography variant="caption">
        version {process.env.REACT_APP_VERSION} {isDevelopment() ? "(dev)" : ""}
      </Typography>
    </>
  );
};

export default App;
